/**
 * Transaction Data 資料執行型態
 */

export const activateStatus =
{
    /// <summary>
        /// 停用
        /// </summary>
        InActive : 0,

        /// <summary>
        /// 啟用
        /// </summary>
        Active : 1,

        /// <summary>
        /// 處理完畢
        /// </summary>
        Complete : 2,

        /// <summary>
        /// 未處理 
        /// </summary>
        Unprocessed : 3,

        /// <summary>
        /// 處理中 
        /// </summary>
        Processing : 4,

        /// <summary>
        /// 異常
        /// </summary>
        Exception : 99
}


