import request from '@/assets/js/iFarmer/helper/httpClientHelper.js'
import {
  httpMethod
} from '@/assets/js/iFarmer/enum/httpMethod.js'
import store from '@/store'

/**
 * 基本資料檔定義
 */

/**
 * JWT Token Cookie Key
 * @returns JWT Token Cookie Key
 */
export const tokenCookieKey = 'jwtToken';

export function getBusinessTypeList(parameters, callback) {
  const param = {
    parameters
  }

  request(store.state.user.token, httpMethod.GET, "/Basic/BusinessTypeList", param, callback);
}

export function postBusinessTypeWithTrans(transData, callback) {
  const param = {
    transData
  }
  request(store.state.user.token, httpMethod.POST, "/Basic/BusinessTypeWithTrans", param, callback);
}

