import {
  command
} from '@/assets/js/iFarmer.v2/enum/command.js';
import {
  getERPStatus,
  getApproveStatus
} from '@/assets/js/iFarmer.v2/library/optionLib';
import {
  activateStatus
} from '@/assets/js/iFarmer.v2/enum/activateStatus.js';
import {
  approveStatusCode
} from '@/assets/js/iFarmer.v2/code/approveStatusCode.js';
import StatusMultiSelect from '@/components/Control/StatusMultiSelect';

export const mixinERP = {
  data: function () {
    return {
      approveStatusOptions: getApproveStatus(),
      statusOptions: getERPStatus(),
      statusCode: activateStatus,
      approveStatusCode: approveStatusCode(),
      command: command,
      commandType: command.Create,
      detailList: [],
      master: null,
      detail: null,
      detailName: undefined,
      isBarginModify: false,
    };
  },
  methods: {
    doAmtCalc(exrate, taxRate, detail) {
      if (detail == undefined || detail.Qty == undefined || detail.UntaxedCurrPrice == undefined) return;
      if (exrate == null || exrate == undefined)
        exrate = 1;
      if (taxRate == null || taxRate == undefined)
        taxRate = 0;
      if (detail.Qty == null || detail.Qty == undefined)
        detail.Qty = 0;
      if (detail.UntaxedCurrPrice == null || detail.UntaxedCurrPrice == undefined)
        detail.UntaxedCurrPrice = 0;
      detail.UntaxedCurrAmt = (parseFloat(detail.Qty) * parseFloat(detail.UntaxedCurrPrice)).toFixed(4);
      detail.UntaxedAmt = (parseFloat(detail.Qty) * parseFloat(detail.UntaxedCurrPrice) * parseFloat(exrate)).toFixed(4);
      detail.TaxedCurrPrice = (parseFloat(detail.UntaxedCurrPrice) * (1 + parseFloat(taxRate))).toFixed(4);
      detail.TaxedCurrAmt = (parseFloat(detail.UntaxedCurrPrice) * (1 + parseFloat(taxRate)) * parseFloat(detail.Qty)).toFixed(4);
      detail.TaxedAmt = (parseFloat(detail.UntaxedCurrPrice) * (1 + parseFloat(taxRate)) * parseFloat(exrate) * parseFloat(detail.Qty)).toFixed(4);
      detail.TaxRate = taxRate;
      detail.Exrate = exrate;
    },
    doSumOfDetail() {
      var vueInstance = this;
      if (vueInstance.master == undefined || vueInstance.detailList.length == 0) return;
      var UntaxedCurrAmt = 0,
        UntaxedAmt = 0,
        TaxedCurrAmt = 0,
        TaxedAmt = 0;

      vueInstance.detailList.forEach(element => {
        UntaxedCurrAmt += parseFloat(element.UntaxedCurrAmt);
        UntaxedAmt += parseFloat(element.UntaxedAmt);
        TaxedCurrAmt += parseFloat(element.TaxedCurrAmt);
        TaxedAmt += parseFloat(element.TaxedAmt);
      });

      vueInstance.master.UntaxedCurrAmt = parseFloat(UntaxedCurrAmt).toFixed(4);
      vueInstance.master.UntaxedAmt = parseFloat(UntaxedAmt).toFixed(4);
      vueInstance.master.TaxedCurrAmt = parseFloat(TaxedCurrAmt).toFixed(4);
      vueInstance.master.TaxedAmt = parseFloat(TaxedAmt).toFixed(4);

      vueInstance.master.CurrencyTaxes = (parseFloat(TaxedCurrAmt) - parseFloat(UntaxedCurrAmt)).toFixed(4);
      vueInstance.master.Taxes = (parseFloat(TaxedAmt) - parseFloat(UntaxedAmt)).toFixed(4);
    },
    doRecalculate() {
      var vueInstance = this;
      if (vueInstance.detailList.length > 0) {
        vueInstance.detailList.forEach(element => {
          vueInstance.doAmtCalc(vueInstance.master.Exrate, vueInstance.master.TaxRate, element);
          if (element.FormID == '' || element.FormID == null || element.FormID == undefined) {
            vueInstance.addTransactionList(
              vueInstance.detailName,
              command.Create,
              element
            );
          }else{
            vueInstance.addTransactionList(
              vueInstance.detailName,
              command.Update,
              element
            );
          }
        });
        vueInstance.doSumOfDetail();
        vueInstance.detailList.splice(
          vueInstance.detailList[0],
          1,
          vueInstance.detailList[0]
        );
      }
    },
    resetBargainUntaxedCurrAmt(){
      var vueInstance = this;
      if(vueInstance.master.BargainUntaxedCurrAmt != undefined && vueInstance.isBarginModify)
          vueInstance.master.BargainUntaxedCurrAmt = null;
    },
    bargain() {
      var vueInstance = this;
      if (vueInstance.master.BargainUntaxedCurrAmt == undefined || vueInstance.detailList.length == 0) return;
      vueInstance.doRecalculate();
      if (parseFloat(vueInstance.master.BargainUntaxedCurrAmt) > parseFloat(vueInstance.master.UntaxedCurrAmt)){
        vueInstance.master.BargainUntaxedCurrAmt = null;
        vueInstance.showAlert(false, '未税议价金额必须小于未税金额');
      }
      else if (vueInstance.master.BargainUntaxedCurrAmt == 0 || vueInstance.master.BargainUntaxedCurrAmt == null) {
        vueInstance.detail.BargainUntaxedCurrPrice = 0;
        vueInstance.detailList.forEach(element => {
          element.BargainUntaxedCurrPrice = 0;

          if (element.FormID == '' || element.FormID == null || element.FormID == undefined) {
            vueInstance.addTransactionList(
              vueInstance.detailName,
              command.Create,
              element
            );
          } else {
            vueInstance.addTransactionList(
              vueInstance.detailName,
              command.Update,
              element
            );
          }
        });
      } else {
        var exrate = vueInstance.master.Exrate;
        var taxRate = vueInstance.master.TaxRate;
        var bargainUntaxedCurrAmt = parseFloat(vueInstance.master.BargainUntaxedCurrAmt);
        var bargainTaxedCurrAmt = (bargainUntaxedCurrAmt * (1 + parseFloat(taxRate))).toFixed(4);
        var bargainUntaxedAmt = (bargainUntaxedCurrAmt * parseFloat(exrate)).toFixed(4);
        var bargainTaxedAmt = (bargainUntaxedCurrAmt * parseFloat(exrate) * (1 + parseFloat(taxRate))).toFixed(4);

        var untaxedCurrAmt = parseFloat(vueInstance.master.UntaxedCurrAmt);
        var taxedCurrAmt = parseFloat(vueInstance.master.TaxedCurrAmt);
        var untaxedAmt = parseFloat(vueInstance.master.UntaxedAmt);
        var taxedAmt = parseFloat(vueInstance.master.TaxedAmt);

        vueInstance.detailList.forEach(detail => {
          var tmpUntaxedCurrAmt = (parseFloat(detail.UntaxedCurrAmt) / untaxedCurrAmt * bargainUntaxedCurrAmt).toFixed(4);
          bargainUntaxedCurrAmt -= tmpUntaxedCurrAmt;
          untaxedCurrAmt -= detail.UntaxedCurrAmt;
          detail.UntaxedCurrAmt = tmpUntaxedCurrAmt;

          var tmpUntaxedAmt = (parseFloat(detail.UntaxedAmt) / untaxedAmt * bargainUntaxedAmt).toFixed(4);
          bargainUntaxedAmt -= tmpUntaxedAmt;
          untaxedAmt -= detail.UntaxedAmt;
          detail.UntaxedAmt = tmpUntaxedAmt;

          var tmpTaxedCurrAmt = (parseFloat(detail.TaxedCurrAmt) / taxedCurrAmt * bargainTaxedCurrAmt).toFixed(4);
          bargainTaxedCurrAmt -= tmpTaxedCurrAmt;
          taxedCurrAmt -= detail.TaxedCurrAmt;
          detail.TaxedCurrAmt = tmpTaxedCurrAmt;

          var tmpTaxedAmt = (parseFloat(detail.TaxedAmt) / taxedAmt * bargainTaxedAmt).toFixed(4);
          bargainTaxedAmt -= tmpTaxedAmt;
          taxedAmt -= detail.TaxedAmt;
          detail.TaxedAmt = tmpTaxedAmt;

          detail.BargainUntaxedCurrPrice = (tmpUntaxedCurrAmt / parseFloat(detail.Qty)).toFixed(4);
          detail.TaxedCurrPrice = (parseFloat(detail.BargainUntaxedCurrPrice) * (1 + parseFloat(taxRate))).toFixed(4);

          vueInstance.addTransactionList(
            vueInstance.detailName,
            command.Update,
            detail
          );
        });
        vueInstance.master.UntaxedCurrAmt = bargainUntaxedCurrAmt;
        vueInstance.master.TaxedCurrAmt = bargainTaxedCurrAmt;
        vueInstance.master.UntaxedAmt = bargainUntaxedAmt;
        vueInstance.master.TaxedAmt = bargainTaxedAmt;
      }
    }
  },
  watch: {
    detailList() {
      this.doSumOfDetail();
    },
    'master.TaxRate'() {
      this.doRecalculate();
      this.resetBargainUntaxedCurrAmt();
    },
    'master.Exrate'() {
      this.doRecalculate();
      this.resetBargainUntaxedCurrAmt();
    },
    'master.BargainUntaxedCurrAmt'() {
      this.bargain();
    },
    'detail.UntaxedCurrPrice'() {
      var vueInstance = this;
      this.doAmtCalc(this.master.Exrate, this.master.TaxRate, this.detail);

      let tmp = $.grep(vueInstance.detailList, function (r) {
        return (r.ID === vueInstance.detail.ID);
      });
      
      if (tmp.length > 0 && vueInstance.detail.UntaxedCurrPrice != tmp[0].UntaxedCurrPrice)
        this.resetBargainUntaxedCurrAmt();
    },
    'detail.Qty'() {
      var vueInstance = this;
      vueInstance.doAmtCalc(this.master.Exrate, this.master.TaxRate, this.detail);

      let tmp = $.grep(vueInstance.detailList, function (r) {
        return (r.ID === vueInstance.detail.ID);
      });
      
      if (tmp.length > 0 && vueInstance.detail.Qty != tmp[0].Qty)
        this.resetBargainUntaxedCurrAmt();
      
    }
  },
  components: {
    'statusMultiSelect': StatusMultiSelect
  }
}



