import Cookies from 'js-cookie'
import request from '@/assets/js/iFarmer/helper/httpClientHelper.js'
import requestV2 from '@/assets/js/iFarmer.v2/helper/httpClientHelper.v2.js'
import {
  httpMethod
} from '@/assets/js/iFarmer/enum/httpMethod.js'
import {
  tokenCookieKey,
  expireCookieKey,
  permissionCookieKey,
  tokenV2CookieKey
} from '@/assets/js/iFarmer/manager/configManager'
import store from '@/store'
import {
  getPermFuncList
} from '@/assets/js/iFarmer/manager/permissionManager'

/**
 * 登入 ICW Portal
 * @param accountNo 使用者帳號
 * @param password 密碼
 * @param callback Callback function
 */
export function test(accountNo, password, callback) {
  const param = {
    accountNo,
    password
  }
}
export function isAuthenticated(accountNo, password, callback) {
  const param = {
    accountNo,
    password
  }

  request(null, httpMethod.POST, '/Auth/Login', param, function (response) {
    if (response) {
      // 取得 v2 token
      requestV2(null, httpMethod.POST, 'Auth', param, function (resp) {
        if (!resp) {
          Cookies.remove(tokenCookieKey);
          Cookies.remove(permissionCookieKey);
          callback(false);
        } else {
          Cookies.set(tokenCookieKey, response.token);
          Cookies.set(tokenV2CookieKey, resp);

          // 設定 Vuex
          store.dispatch("updateTokenAsync", response.token);

          getPermFuncList(function (result) {
            Cookies.set(permissionCookieKey, JSON.stringify(result));
            callback(true);
            return;
          });
        }
      });
    } else {
      Cookies.remove(tokenCookieKey);
      Cookies.remove(permissionCookieKey);
      callback(false);
    }
  });
}

export function isAuthenticatedByToken(token, callback) {
  const param = {
    token
  }

  request(null, httpMethod.POST, '/Auth/Token', param, function (response) {
    if (response) {
      // 取得 v2 token
      requestV2(null, httpMethod.POST, 'Auth/Token', param, function (resp) {
        if (!resp) {
          Cookies.remove(tokenCookieKey);
          Cookies.remove(permissionCookieKey);
          callback(false);
        } else {
          Cookies.set(tokenCookieKey, response.token);
          Cookies.set(tokenV2CookieKey, resp);

          // 設定 Vuex
          store.dispatch("updateTokenAsync", response.token);

          getPermFuncList(function (result) {
            Cookies.set(permissionCookieKey, JSON.stringify(result));
            callback(true);
            return;
          });
        }
      });
    } else {
      Cookies.remove(tokenCookieKey);
      Cookies.remove(permissionCookieKey);
      callback(false);
    }
  });
}

/**
 * 登出
 */
export function logout(expired) {
  // 刪除 cookie
  Cookies.remove(tokenCookieKey);
  Cookies.remove(tokenV2CookieKey);
  Cookies.remove(permissionCookieKey);

  if (expired)
    Cookies.set(expireCookieKey, true);

  // 清空 Vuex
  store.dispatch("updateTokenAsync", "");
  location.reload();
}

/**
 * 取得 JWT Token
 * @returns JWT Token
 */
export function getToken() {
  return Cookies.get(tokenCookieKey);
}

/**
 * 變更密碼
 * @param accountNo 使用者帳號
 * @param oldPassword 舊密碼
 * @param newPassword 新密碼
 * @returns Callback function
 */
export function changePassword(accountNo, oldPassword, newPassword, callback) {
  const param = {
    accountNo,
    oldPassword,
    newPassword
  }

  request(null, httpMethod.POST, '/Auth/Password', param, function (response) {
    if (response) {
      callback(response);
      return;
    }

    callback(false);
  });
}

export function getUserAccountList(parameters, callback) {
  const param = {
    parameters
  }

  request(store.state.user.token, httpMethod.GET, "/Auth/UserAccountList", param, callback);
}

export function postUserAccount(transData, callback) {
  const param = {
    transData
  }

  request(store.state.user.token, httpMethod.POST, "/Auth/UserAccountWithTrans", param, callback);
}

export function getSiteInfo(callback) {
  request(null, httpMethod.GET, "/Auth/SiteInfo", null, callback);
}


