import {
  getGuid
} from "@/assets/js/iFarmer/library/commonLib";
export function purchaseRequest() {
    return {
      ID: getGuid(),
      FormID: undefined,
      PurchaseTypeCode: undefined,
      RequestDate: undefined,
      RequestDateLabel: undefined,
      DeptCode: undefined,
      RequestEmpNo: undefined,
      SupplierNo: undefined,
      RequiredDate: undefined,
      CurrencyCode: undefined,
      Exrate:undefined,
      TaxCode: undefined,
      TaxRate: undefined,
      UntaxedCurrAmt: undefined,
      UntaxedAmt: undefined,
      TaxedCurrAmt: undefined,
      TaxedAmt:undefined,
      CurrencyTaxes: undefined,
      Taxes:undefined,
      ApproveStatus: undefined,
      Status:undefined,
      SiteID: undefined,
      ApproveDate: undefined,
      ApproveUserID: undefined,
      ApproveUserName: undefined,
      VoidDate: undefined,
      VoidUserID:undefined,
      CreatedUserID: undefined,
      CreatedDatetime: undefined,
      ModifiedUserID: undefined,
      ModifiedDatetime: undefined, 
      PurchaseTypeName: null,
      PurchaseIsMaterial: null,
      PurchaseSwineType: null,
      PurchaseSwineState: null,
      DeptName: null,
      RequestEmpName: null,
      SupplierName: null,
      CurrencyName: null,
      TaxName: null,
      ApproveStatusName: null,
      StatusName: null,
      CreatedUserName: null,
      ModifiedUserName: null,
      VoidUserName: null
    }
  }
  


