import {
  getGuid
} from "@/assets/js/iFarmer/library/commonLib";
export function purchaseOrderItem() {
  return {
    ID: getGuid(),
    FormID: undefined,
    RequestFormID: undefined,
    RequestDeptCode: undefined,
    ItemNo: undefined,
    ItemName: undefined,
    LotNo:undefined,
    Qty: 0,
    UOM: undefined,
    Exrate: 0,
    ExchangeRate: undefined,
    StockQty: 0,
    StockUOM: undefined,
    StockUOMName: undefined,
    CurrencyCode: undefined,
    TaxCode: undefined,
    TaxRate: undefined,
    QuotePrice: 0,
    UntaxedCurrPrice: undefined,
    TaxedCurrPrice: undefined,
    UntaxedCurrAmt: undefined,
    UntaxedAmt: undefined,
    TaxedCurrAmt: undefined,
    TaxedAmt: undefined,
    DeliveredQty: undefined,
    Status: undefined,
    SiteID: undefined,
    CreatedDatetime: undefined,
    CreatedUserID: undefined,
    ModifiedDatetime: undefined,
    ModifiedUserID: undefined,    
    UOMName: null,
    CurrencyName: null,
    TaxName: null,
    RequestDeptName: null,
    StatusName: null,
    ApproveStatus: null,
    PurchaseTypeName: null,
    PurchaseIsMaterial: null,
    PurchaseSwineType: null,
    PurchaseSwineState: null,
    PurchaseEmpNo: null,
    PurchaseEmpName: null,
    SupplierNo: null,
    SupplierName: null,
    PurchaseDate: null,
    CreatedUserName: null,
    ModifiedUserName: null,
    RequestEmpName: null,
    RequestDate: null
};
}


