import request from '@/assets/js/iFarmer/helper/httpClientHelper.js'
import {
  httpMethod
} from '@/assets/js/iFarmer/enum/httpMethod.js'
import store from '@/store'

/**
 * 取得所有通知集合
 * @param callback
 */

export function getPermissionRoleList(companyCode, status, empNo, code, callback) {
  const param = {
    companyCode,
    status,
    empNo,
    code
  }

  request(store.state.user.token, httpMethod.GET, "/Permission/PermissionRoleList", param, callback);
}


export function postPermissionRole(permissionRole, callback) {
  const param = {
    permissionRole
  }

  request(store.state.user.token, httpMethod.POST, "/Permission/PermissionRole", param, callback);
}

export function getFunctionMenuList(empNo, callback) {
  const param = {
    empNo
  }

  request(store.state.user.token, httpMethod.GET, "/Permission/FunctionMenu", param, function (response) {
    return response.IsSuccess ? callback(response.Data) : callback(null);
  });
}

export function getPermFuncList(callback) {
  request(store.state.user.token, httpMethod.GET, "/Permission/PermFuncList", null, function (response) {

    return response.IsSuccess ? callback(response.Data) : callback(null);
  });
}

export function getProgramList(parentCode, callback) {
  const param = {
    parentCode
  }
  request(store.state.user.token, httpMethod.GET, "/Permission/ProgramList", param, callback);
}

export function getAllPermissionRoleList(parameters, callback) {
  const param = {
    parameters
  }
  request(store.state.user.token, httpMethod.GET, "/Permission/PermissionRoleList", param, callback);
}

export function getPermissionList(targetCode, callback) {
  const param = {
    targetCode
  }
  request(store.state.user.token, httpMethod.GET, "/Permission/PermissionList", param, callback);
}

export function getPermissionRoleDetailList(roleCode, callback) {
  const param = {
    roleCode
  }
  request(store.state.user.token, httpMethod.GET, "/Permission/PermissionRoleDetailList", param, callback);
}

export function PostPermissionWithTrans(transData, callback) {
  const param = {
    transData
  }
  request(store.state.user.token, httpMethod.POST, "/Permission/PermissionWithTrans", param, callback);
}



