export function getERPStatus() {
  return [{
      label: "未处理",
      value: 3
    },
    {
      label: "处理中",
      value: 4
    },
    {
      label: "处理完毕",
      value: 2
    },
    {
      label: "已删除",
      value: 0
    }
  ]
}
export function getStatus() {
  return [
    {
      label: "启用",
      value: 1
    },
    {
      label: "停用",
      value: 0
    }
  ]
}

export function getApproveStatus() {
  return [{
      label: "暂存",
      value: 'N'
    },
    {
      label: "传签中",
      value: 'F'
    },
    {
      label: "退件",
      value: 'R'
    },
    {
      label: "已核准",
      value: 'A'
    },
    {
      label: "作废",
      value: 'V'
    }
  ]
}


