import {
  command
} from '@/assets/js/iFarmer.v2/enum/command.js';
import {
  postData
} from '@/assets/js/iFarmer.v2/manager/genericManager';
import store from "@/store";

export const mixinGeneric = {
  data: function () {
    return {
      commandType: command.Create,
      functionName: '',
      detailTitle: '',
      detailList: [],
      selectedDetailRows: [],
      tmpTransList: [],
      transactionList: [],
      serviceName: undefined,
      master: null,
      masterName: undefined,
      detail: null,
      detailName: undefined,
      dlgDetail: false,
      loginAccount: store.getters.userProfile.AccountNo,
      loginEmpNo: store.getters.userProfile.EmpNo,
      loginEmpName: store.getters.userProfile.EmpName,
      loginDeptCode: store.getters.userProfile.DeptCode,
      loginDeptName: store.getters.userProfile.DeptName,
      historyID: store.getters.getHistoryFormID,
      isModify: store.getters.getisModify,
      pageReadOnly: false
    };
  },
  methods: {
    showAlert(type, msg) {
      if (type)
        this.$notify.success({
          title: '提示',
          message: msg
        });
      else
        this.$notify.warning({
          title: '警告',
          message: msg
        });
    },
    resetForm(formName) {
      if (this.$refs[formName] != undefined)
        this.$refs[formName].resetFields();
    },
    getUnique(arr, comp) {
      const unique = arr.map(e => e[comp]).map((e, i, final) => final.indexOf(e) === i && i).filter(e => arr[e]).map(e => arr[e]);
      return unique;
    },
    initMaster() {
      this.pageReadOnly = false;
    },
    initDetail() {

    },
    validateMaster(master) {
      return true;
    },
    validateDetail(detail) {
      return true;
    },
    editDetail() {
      var vueInstance = this;
      vueInstance.tmpTransList = [];
      vueInstance.dlgDetail = true;
      if (vueInstance.selectedDetailRows.length === 0) {
        vueInstance.$nextTick(function () {
          vueInstance.resetForm('detail');
          vueInstance.initDetail();
        });
        vueInstance.detailTitle = '新增' + vueInstance.functionName;
      } else {
        vueInstance.detailTitle = '维护' + vueInstance.functionName;
        vueInstance.detail = Object.assign({}, vueInstance.selectedDetailRows[0]);
      }
    },
    createNextDetail() {
      var vueInstance = this;
      if (vueInstance.validateDetail(vueInstance.detail)) {
        let tmpDetail = Object.assign({}, vueInstance.detail);
        vueInstance.tmpTransList.push(tmpDetail);
        vueInstance.initDetail();
      }
    },
    saveDetail() {
      var vueInstance = this;
      if (vueInstance.validateDetail(vueInstance.detail)) {
        let tmpDetail = Object.assign({}, vueInstance.detail);
        vueInstance.tmpTransList.push(tmpDetail);
        var cmd;

        if (vueInstance.selectedDetailRows.length === 0)
          cmd = command.Create;
        else
          cmd = command.Update;

        vueInstance.tmpTransList.forEach(element => {
          if (element.FormID == undefined)
            element.CreatedUserID = vueInstance.loginAccount;
          else
            element.ModifiedUserID = vueInstance.loginAccount;

          vueInstance.addTransactionList( 
            vueInstance.detailName,
            cmd,
            element
          );
          let tmp = $.grep(vueInstance.detailList, function (r) {
            return r.ID === element.ID;
          });

          if (tmp.length > 0)
            vueInstance.detailList.splice(
              vueInstance.detailList.indexOf(tmp[0]),
              1,
              element
            );
          else
            vueInstance.detailList.push(element);
        });
        vueInstance.selectedDetailRows.length = 0;
        vueInstance.dlgDetail = false;
      }
    },
    deleteDetail() {
      var vueInstance = this;
      vueInstance.addTransactionList(
        vueInstance.detailName,
        command.Delete,
        vueInstance.selectedDetailRows[0]
      );
      let tmp = $.grep(vueInstance.detailList, function (r) {
        return (r.ID === vueInstance.selectedDetailRows[0].ID);
      });
      if (tmp.length > 0)
        vueInstance.detailList.splice(vueInstance.detailList.indexOf(tmp[0]), 1);

      vueInstance.dlgDetail = false;
      vueInstance.selectedDetailRows.length = 0;
    },
    save() {
      var vueInstance = this;
      if (vueInstance.validateMaster(vueInstance.master)) {
        vueInstance.loadingText = '资料处理中';

        if (vueInstance.commandType != command.Create)
          vueInstance.$set(vueInstance.master, "ModifiedUserID", vueInstance.loginAccount);
        else
          vueInstance.$set(vueInstance.master, "CreatedUserID", vueInstance.loginAccount);

        let tmpLst = [{
          ID: vueInstance.master.ID,
          SeqNo: 0,
          Name: vueInstance.masterName,
          Command: vueInstance.commandType,
          Entity: vueInstance.master
        }];
console.log(vueInstance.transactionList)
        postData({
            ServiceName: vueInstance.serviceName,
            TaskList: tmpLst.concat(vueInstance.transactionList)
          },
          function (response) {
            vueInstance.loadingText = null;
            if (response.IsSuccess) {
              vueInstance.initMaster();
              vueInstance.showAlert(true, '资料储存成功');
            } else {
              var errorMsg = "",
                errorCount = 0;
              response.ExceptionList[0].forEach(result => {
                errorMsg += ++errorCount + "." + result.ExceptionMessage;
              });
              vueInstance.showAlert(false, errorMsg);
            }
          });

      }
    },
    deleteAll() {
      var vueInstance = this;
      if (vueInstance.commandType != command.Create) {
        vueInstance.loadingText = '资料处理中';
        postData({
            ServiceName: vueInstance.serviceName,
            TaskList: [{
              ID: vueInstance.master.ID,
              SeqNo: 0,
              Name: vueInstance.masterName,
              Command: command.Delete,
              Entity: vueInstance.master
            }]
          },
          function (response) {
            vueInstance.loadingText = null;
            if (response.IsSuccess) {
              vueInstance.initMaster();
              vueInstance.showAlert(true, '资料删除成功');
            } else {
              var errorMsg = "",
                errorCount = 0;
              response.ExceptionList[0].forEach(result => {
                errorMsg += ++errorCount + "." + result.ExceptionMessage;
              });
              vueInstance.showAlert(false, errorMsg);
            }
          });
      } else vueInstance.initMaster();
    },
    addTransactionList(name, action, transEntity) {
      var vueInstance = this;
      let trans = {},
        tmp = [];
      let entity = Object.assign({}, transEntity);

      vueInstance.$set(trans, 'ID', entity.ID);
      vueInstance.$set(trans, 'SeqNo', vueInstance.transactionList.length + 1);
      vueInstance.$set(trans, 'Name', name);
      vueInstance.$set(trans, 'Command', action);
      vueInstance.$set(trans, 'Entity', entity);

      if (action == command.Create || action == command.Update) {
        let tmp = $.grep(vueInstance.transactionList, function (r) {
          return r.Entity.ID === entity.ID;
        });
        if (tmp.length > 0)
          tmp[0].Entity = entity;
        else
          vueInstance.transactionList.push(trans);
      } else {
        let tmp = $.grep(vueInstance.transactionList, function (r) {
          return (r.Entity.ID === entity.ID);
        });
        if (tmp.length > 0)
          vueInstance.transactionList.splice(vueInstance.transactionList.indexOf(tmp[0]), 1);

        if (entity.FormID != null && entity.FormID != '') {
          entity.ModifiedUserID = vueInstance.loginAccount;
          vueInstance.transactionList.push(trans);
        }
      }
    },
    approveAction(cmd) {
      var vueInstance = this;
      let tmpLst = [{
        ID: vueInstance.master.ID,
        SeqNo: 0,
        Name: vueInstance.masterName,
        Command: cmd,
        Entity: vueInstance.master
      }];

      if (cmd == command.Approve)
        vueInstance.$set(vueInstance.master, "ApproveUserID", vueInstance.loginAccount);
      else if (cmd == command.VoidUserID)
        vueInstance.$set(vueInstance.master, "VoidUserID", vueInstance.loginAccount);

      vueInstance.loadingText = '资料处理中';
      postData({
          ServiceName: vueInstance.serviceName,
          TaskList:  (cmd != command.Apply) ? tmpLst: tmpLst.concat(vueInstance.transactionList)
          
        },
        function (response) {
          vueInstance.loadingText = null;
          if (response.IsSuccess) {
            vueInstance.initMaster();
            vueInstance.showAlert(true, '资料变更成功');
            if(vueInstance.historyID != null && !vueInstance.isModify)
               window.location.reload();
          } else {
            var errorMsg = "",
              errorCount = 0;
            response.ExceptionList[0].forEach(result => {
              errorMsg += ++errorCount + "." + result.ExceptionMessage;
            });
            vueInstance.showAlert(false, errorMsg);
          }
        }
      );
    },
  },
  watch: {
    'master.ApproveStatus'() {
      var vueInstance = this;
      if (vueInstance.master.ApproveStatus == vueInstance.approveStatusCode.Apply ||
        vueInstance.master.ApproveStatus == vueInstance.approveStatusCode.Approve ||
        vueInstance.master.ApproveStatus == vueInstance.approveStatusCode.Invalid)
        vueInstance.pageReadOnly = true;
      else
        vueInstance.pageReadOnly = false;
    },
  }
}



