import {
  getCurrencyList,
  getTaxTypeList,
  getPaymentTermList,
} from '@/assets/js/iFarmer/manager/basicManager.js';
import {
  getItemUnitList,
  getWarehouseList,
  getWarehouseZoneList,
  getWarehouseBinList,
} from "@/assets/js/iFarmer/manager/configManager";
import {
  getDepartmentList
} from '@/assets/js/iFarmer/manager/OrganizationManager';
import {
  getData,
} from '@/assets/js/iFarmer.v2/manager/genericManager';

export const mixinOption = {
  data: function () {
    return {
      currencyOption: [],
      taxTypeOption: [],
      paymentTermOption: [],
      departOption: [],
      itemUnitOption: [],
      warehouseOption: [],
      warehouseZoneOption: [],
      warehouseBinOption: [],
      purchaseTypeOption: [],
      salesTypeOption: [],
    };
  },
  methods: {
    getCurrency() {
      var vueInstance = this;
      let filter = {};
      getCurrencyList(filter, function (response) {
        if (response.IsSuccess) {
          vueInstance.currencyOption = response.Data;
        }
      });
    },
    getExrate() {
      var vueInstance = this;
      if (vueInstance.master.CurrencyCode == 'RMB') {
        vueInstance.$set(vueInstance.master, 'Exrate', 1);
      } else {
        vueInstance.$set(vueInstance.master, 'Exrate', undefined);
      }
    },
    getTaxType() {
      var vueInstance = this;
      getTaxTypeList({}, function (response) {
        if (response.IsSuccess) {
          vueInstance.taxTypeOption = response.Data;
        }
      });
    },
    getTaxRate() {
      var vueInstance = this;
      let tempTaxRat = vueInstance.taxTypeOption.filter(function (element) {
        return vueInstance.master.TaxCode == element.TaxCode;
      });
      vueInstance.$set(vueInstance.master, 'TaxRate', tempTaxRat[0].TaxRate);
    },
    getDepartment() {
      var vueInstance = this;
      getDepartmentList({}, function (response) {
        if (response.IsSuccess) {
          vueInstance.departOption = response.Data;
        }
      });
    },
    getItemUnit(filter) {
      var vueInstance = this;
      vueInstance.itemUnitOption = [];
      vueInstance.loadingText = "資料搜尋中";
      getItemUnitList(filter, function (response) {
        vueInstance.loadingText = null;
        if (response.IsSuccess) {
          vueInstance.itemUnitOption = response.Data;
        }
      });
    },
    getPaymentTerm() {
      var vueInstance = this;
      let filter = {};
      getPaymentTermList(filter, function (response) {
        vueInstance.loadingText = null;
        if (response.IsSuccess) {
          vueInstance.paymentTermOption = response.Data;
        }
      });
    },
    getPurchaseType(filter) {
      var vueInstance = this;
      vueInstance.purchaseTypeOption = [];

      getData('PurchaseType', filter, function (response) {
        if (response) {
          vueInstance.purchaseTypeOption = response;
        }
      });
    },
    getSalesType() {
      var vi = this;
      getData('SalesType', {}, function (response) {
        if (response) {
          vi.salesTypeOption = [];
          vi.salesTypeOption = response;
        }
      });
    },
    getWarehouse() {
      var vueInstance = this;
      getWarehouseList({}, function (response) {
        if (response.IsSuccess) {
          vueInstance.warehouseOption = response.Data;
        }
      });
    },
    getWarehouseZone() {
      var vueInstance = this;
      getWarehouseZoneList({}, function (response) {
        if (response.IsSuccess) {
          vueInstance.warehouseZoneOption = response.Data;
        }
      });
    },
    getWarehouseBin() {
      var vueInstance = this;
      getWarehouseBinList({}, function (response) {
        if (response.IsSuccess) {
          vueInstance.warehouseBinOption = response.Data;
        }
      });
    },
  }
}



