import request from '@/assets/js/iFarmer/helper/httpClientHelper.js'
import {
  httpMethod
} from '@/assets/js/iFarmer/enum/httpMethod.js'
import store from '@/store'

/**
 * 取得所有通知集合
 * @param callback
 */

export function getCompanyList(parameters, callback) {
  const param = {
    parameters
  }

  request(store.state.user.token, httpMethod.GET, "/Organization/CompanyList", param, callback);
}

export function getDepartmentList(parameters, callback) {
  const param = {
    parameters
  }

  request(store.state.user.token, httpMethod.GET, "/Organization/DepartmentList", param, callback);
}

export function getDeptListByName(deptName,parameters, callback) {
  const param = {
    deptName,
    parameters
  }

  request(store.state.user.token, httpMethod.GET, "/Organization/DeptListByName", param, callback);
}

export function postCompanyAndDepartment(transData, callback) {
  const param = {
    transData
  }

  request(store.state.user.token, httpMethod.POST, "/Organization/CompAndDeptWithTrans", param, callback);
}

export function getEmployeeList(parameters, callback) {
  const param = {
    parameters
  }

  request(store.state.user.token, httpMethod.GET, "/Organization/EmployeeList", param, callback);
}

export function getEmployeeListByName(empName,parameters, callback) {
  const param = {
    empName,
    parameters
  }
  request(store.state.user.token, httpMethod.GET, "/Organization/EmployeeListByName", param, callback);
}

export function postEmployee(transData, callback) {
  const param = {
    transData
  }

  request(store.state.user.token, httpMethod.POST, "/Organization/EmpWithTrans", param, callback);
}


