import {
  getGuid
} from "@/assets/js/iFarmer/library/commonLib";
export function supplier() {
  return {
    ID: getGuid(),
    SupplierNo: undefined,
    CompCode: undefined,
    CompName: undefined,

    CountryCode: undefined,
    CountryName: undefined,

    Name: undefined,
    DisplayText: undefined,
    CityCode: undefined,
    CityName: undefined,

    PostalCode: undefined,
    Street: undefined,
    IndustryCode: undefined,
    Representative: undefined,
    RegistrationNo: undefined,
    Contact: undefined,
    Phone1: undefined,
    Phone2: undefined,
    Fax: undefined,
    TaxNo: undefined,
    CreditNo: undefined,
    BusinessType: undefined,
    LocationCode: undefined,
    BankCode: undefined,
    BankName: undefined,
    BankAccount: undefined,
    Quality: undefined,
    PaymentTerm: undefined,
    CreditLimit: undefined,
    CreditStatus: undefined,
    Remark: undefined,
    SiteID: undefined,
    Status: 1,
    CreatedUserID: undefined,
    CreatedDatetime: undefined,
    ModifiedUserID: undefined,
    ModifiedDatetime: undefined
  }
}



