import request from '@/assets/js/iFarmer/helper/httpClientHelper.js'
import {
  httpMethod
} from '@/assets/js/iFarmer/enum/httpMethod.js'
import store from '@/store'

/**
 * 取得所有通知集合
 * @param callback
 */

export function getLocationList(parameters, callback) {
  const param = {
    parameters
  }
  request(store.state.user.token, httpMethod.GET, "/Location/LocationList", param, callback);
}

export function getLocationListByName(locationName, parameters, callback) {
  const param = {
    locationName,
    parameters
  }
  request(store.state.user.token, httpMethod.GET, "/Location/LocationListByName", param, callback);
}

export function getLocationTypeList(parameters, callback) {
  const param = {
    parameters
  }
  request(store.state.user.token, httpMethod.GET, "/Location/LocationTypeList", param, callback);
}

export function getSiteList(parameters, callback) {
  const param = {
    parameters
  }
  request(store.state.user.token, httpMethod.GET, "/Location/SiteList", param, callback);
}

export function getAuthSiteList(callback) {
  request(store.state.user.token, httpMethod.GET, "/Location/AuthSiteList", null, callback);
}

export function postSiteWithTrans(transData, callback) {
  const param = {
    transData
  }
  request(store.state.user.token, httpMethod.POST, "/Location/SiteWithTrans", param, callback);
}

export function getZoneList(parameters, callback) {
  const param = {
    parameters
  }
  request(store.state.user.token, httpMethod.GET, "/Location/ZoneList", param, callback);
}

export function postZoneWithTrans(transData, callback) {
  const param = {
    transData
  }
  request(store.state.user.token, httpMethod.POST, "/Location/ZoneWithTrans", param, callback);
}

export function getHouseList(parameters, callback) {
  const param = {
    parameters
  }
  request(store.state.user.token, httpMethod.GET, "/Location/HouseList", param, callback);
}

export function postHouseWithTrans(transData, callback) {
  const param = {
    transData
  }
  request(store.state.user.token, httpMethod.POST, "/Location/HouseWithTrans", param, callback);
}

export function getPigstyList(parameters, callback) {
  const param = {
    parameters
  }
  request(store.state.user.token, httpMethod.GET, "/Location/PigstyList", param, callback);
}

export function postPigstyWithTrans(transData, callback) {
  const param = {
    transData
  }
  request(store.state.user.token, httpMethod.POST, "/Location/PigstyWithTrans", param, callback);
}



