import request from '@/assets/js/iFarmer.v2/helper/httpClientHelper.v2.js'
import {
  httpMethod
} from '@/assets/js/iFarmer/enum/httpMethod.js'
import Cookies from 'js-cookie'
import {
  tokenV2CookieKey
} from '@/assets/js/iFarmer/manager/configManager'


export function getData(model,parameters, callback) {
  const param = {
    model,
    parameters
  }
  request(Cookies.get(tokenV2CookieKey), httpMethod.GET, '/Repository', {
    model,
    parameters
  }, callback);
}

export function postData(postData, callback) {
  request(Cookies.get(tokenV2CookieKey), httpMethod.POST,"/Repository", postData, callback);
}

export function getDataByFunc(funcName, parameters, callback) {
  const param = {
    funcName,
    parameters
  }
  request(Cookies.get(tokenV2CookieKey), httpMethod.GET, '/Repository/ByFunc', {
    funcName,
    parameters
  }, callback);
}


