export function itemStorage() {
    return {
        ID: "",
        StorageCode: undefined,
        CompCode: undefined,
        Name: undefined,
        SiteID: undefined,
        Status: undefined,
        CreatedUserID: undefined,
        CreatedDatetime: undefined,
        ModifiedUserID: undefined,
        ModifiedDatetime: undefined,
    }
}


