import axios from 'axios'
import {
  httpMethod
} from '@/assets/js/iFarmer/enum/httpMethod.js'
import {
  logout
} from '@/assets/js/iFarmer/manager/authManager.js'

/**
 * HTTP Request
 * @param token Jwt token
 * @param method HTTP Method
 * @param requestUri Request Uri(不包含网站)
 * @param parameters 参数集合
 * @param callback 回传 function
 */
export default function (token, method, requestUri, parameters, callback) {
  const isPro = Object.is(process.env.NODE_ENV, 'production');
  axios.defaults.baseURL = isPro ? '/api' : 'dev/api';

  switch (method) {
    case httpMethod.GET:
      requestGET(token, requestUri, parameters, callback);
      break;
    case httpMethod.POST:
      requestPOST(token, requestUri, parameters, callback);
      break;
    case httpMethod.PUT:
      requestPUT(token, requestUri, parameters, callback);
      break;
    case httpMethod.DELETE:
      reqestDELETE(token, requestUri, parameters, callback);
      break;
  }
}

/**
 * Send GET Request
 * @param token JwtToken
 * @param requestUri Api
 * @param parameters Parameters
 * @param callback Response callback function
 */
export function requestDownload(token, requestUri, parameters, callback) {
  const isPro = Object.is(process.env.NODE_ENV, 'production');
  axios.defaults.baseURL = isPro ? '/api' : 'dev/api';

  axios.get(requestUri, {
    responseType: 'arraybuffer',
    params: parameters,
    headers: {
      "Authorization": `Bearer ${token}`
    }
  }).then(response => {
    callback(response);
  }).catch(error => {
    callback(error.response);
  });

  // 因需加入验证，调整回传型态作法
  // }).then(response => {
  //     // success callback
  //     callback(response.data);
  //   }).catch(error => {
  //     // error callback
  //     callback(null);
  //   });
}

/**
 * Send GET Request
 * @param token JwtToken
 * @param requestUri Api
 * @param parameters Parameters
 * @param callback Response callback function
 */
function requestGET(token, requestUri, parameters, callback) {
  axios.get(requestUri, {
    params: parameters,
    headers: {
      "Authorization": `Bearer ${token}`
    }
  }).then(response => {
    // success callback
    if (Array.isArray(response.data.Data) && response.data.Data.length >= 10000) {
      if (!confirm('共查询到 ' + response.data.Data.length + ' 笔, 是否确认查询(建议筛选条件后重新查询), 点选 [是] 将继续查询(可能影响效能)')) {
        response.data.Data = [];
      }
    }

    callback(response.data);
  }).catch(error => {
    if (token !== null && error.response.status === 401) {
      logout(true);
    } else {
      callback(null);
    }
  });
}

/**
 * Send POST Request
 * @param token JwtToken
 * @param requestUri Api
 * @param parameters Parameters
 * @param callback Response callback function
 */
function requestPOST(token, requestUri, parameters, callback) {
  axios.post(requestUri,
    parameters, {
      headers: {
        "Authorization": `Bearer ${token}`
      }
    }).then(response => {
    // success callback
    callback(response.data);
  }).catch(error => {
    if (token !== null && error.response.status === 401) {
      logout(true);
    } else {
      callback(null);
    }
  });
}

/**
 * Send PUT Request
 * @param token JwtToken
 * @param requestUri Api
 * @param parameters Parameters
 * @param callback Response callback function
 */
function requestPUT(token, requestUri, parameters, callback) {
  axios.put(requestUri,
    parameters, {
      headers: {
        'Authorization': `Bearer ${token}`
      }
    }).then(response => {
    // success callback
    callback(response.data);
  }).catch(error => {
    if (token !== null && error.response.status === 401) {
      logout(true);
    } else {
      callback(null);
    }
  });
}

/**
 * Send DELETE Request
 * @param token JwtToken
 * @param requestUri Api
 * @param parameters Parameters
 * @param callback Response callback function
 */
function reqestDELETE(token, requestUri, parameters, callback) {
  axios.delete(requestUri, {
    params: parameters,
    headers: {
      "Authorization": `Bearer ${token}`
    }
  }).then(response => {
    // success callback
    callback(response.data);
  }).catch(error => {
    if (token !== null && error.response.status === 401) {
      logout(true);
    } else {
      callback(null);
    }
  });
}



